* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--font-family);
  background: var(--slate-100);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

:root {
  --lime: hsl(61, 70%, 52%);
  --lime-hover: hsl(60, 72%, 93%);
  --red: hsl(4, 69%, 50%);
  --white: hsl(0, 0%, 100%);
  --slate-100: hsl(202, 86%, 94%);
  --slate-300: hsl(203, 41%, 72%);
  --slate-500: hsl(200, 26%, 54%);
  --slate-700: hsl(200, 24%, 40%);
  --slate-900: hsl(202, 55%, 16%);
  --slate-900-repayment: hsl(202, 56%, 12%);
}

:root {
  --font-family: "Plus Jakarta Sans", sans-serif;
}

h1,
h2 {
  font-size: 1.25rem;
  color: var(--slate-900);
}
@media (min-width: 40em) {
  h1,
  h2 {
    font-size: 1.5rem;
  }
}

.container {
  max-width: none;
  background: var(--white);
  display: flex;
  flex-direction: column;
}
@media (min-width: 40em) {
  .container {
    flex-direction: row;
    align-items: stretch;
    border-radius: 1.25rem;
  }
}
@media (min-width: 40em) {
  .container > section {
    width: 50%;
    flex-shrink: 0;
  }
}

.form {
  padding: 1.5rem;
}
.form__values__fieldset {
  border: none;
  margin-bottom: 1.25rem;
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}
@media (min-width: 40em) {
  .form__values__fieldset {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    margin-bottom: 1.5rem;
    gap: 1.5rem;
  }
}
.form__values__fieldset__input-group {
  min-width: 0;
}
@media (min-width: 40em) {
  .form__values__fieldset__input-group:first-child {
    grid-column: 1/span 2;
  }
}
.form__values__fieldset__input-group__title {
  margin-bottom: 0.5rem;
  color: var(--slate-700);
  display: inline-block;
}
.form__values__fieldset__input-group__input-container {
  border: 0.0625rem solid var(--slate-700);
  display: flex;
  border-radius: 0.3125rem;
  overflow: hidden;
  align-items: center;
  min-width: 0;
}
.form__values__fieldset__input-group__input-container:hover .form__values__fieldset__input-group__input-container__span, .form__values__fieldset__input-group__input-container:focus .form__values__fieldset__input-group__input-container__span {
  background: var(--lime);
}
.form__values__fieldset__input-group__input-container:hover, .form__values__fieldset__input-group__input-container:focus {
  border: 0.0625rem solid var(--lime);
}
.form__values__fieldset__input-group__input-container__span {
  background: var(--slate-300);
  font-size: 1.125rem;
  padding: 0.75rem 1rem;
  color: var(--slate-900);
}
.form__values__fieldset__input-group__input-container__input {
  border: none;
  font-size: 1.125rem;
  padding: 0.75rem 1rem;
  outline: none;
  flex: 1;
  width: 0;
  min-width: 0;
}
.form__values__submit-btn {
  width: 100%;
  padding: 0.75rem 1rem;
  font-size: 1.125rem;
  border-radius: 2.625rem;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--lime);
  gap: 0.5rem;
}
@media (min-width: 40em) {
  .form__values__submit-btn {
    width: fit-content;
  }
}
.form__type {
  border: none;
  margin-bottom: 1.25rem;
}
@media (min-width: 40em) {
  .form__type {
    margin-bottom: 1.5rem;
  }
}
.form__type__title {
  color: var(--slate-700);
}
.form__type__input-group {
  width: 100%;
  padding: 0.75rem 1rem;
  border: 0.0625rem solid var(--slate-700);
  margin-top: 0.5rem;
  border-radius: 0.3125rem;
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 0.5rem;
}
.form__type__input-group:hover, .form__type__input-group:focus {
  background: var(--lime-hover);
  border-color: var(--lime);
}
.form__type__input-group__input {
  accent-color: var(--lime);
  width: 1.25rem;
  height: 1.25rem;
  cursor: pointer;
}
.form__type__input-group__title {
  color: var(--slate-900);
  font-size: 1rem;
  font-weight: 900;
}

.header {
  display: flex;
  flex-direction: column;
  margin-bottom: 1.25rem;
  gap: 0.625rem;
}
@media (min-width: 40em) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.5rem;
  }
}
.header__title {
  font-size: 1.5rem;
  line-height: 1.2;
}
.header__clear-btn {
  font-size: 1rem;
  font-weight: 500;
  border: none;
  background: transparent;
  text-decoration: underline;
  color: var(--slate-700);
}

.result {
  background: var(--slate-900);
  padding: 1.5rem;
  border-radius: 0 1.25rem 1.25rem 5rem;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 40em) {
  .result {
    border-radius: 0;
  }
}
.result__empty {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
}
.result__empty__title {
  color: var(--white);
}
.result__empty__text {
  color: var(--slate-700);
  text-align: center;
}
.result__completed {
  display: none;
  flex-direction: column;
  gap: 1.5rem;
}
.result__completed__title {
  color: var(--white);
}
.result__completed__text {
  color: var(--slate-700);
}
.result__completed--group {
  border-top: 0.1875rem solid var(--lime);
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  border-radius: 0.3125rem;
  background: var(--slate-900-repayment);
  padding: 1.5rem;
}
.result__completed--group:first-child {
  border-bottom: 0.0625rem solid var(--slate-700);
}
.result__completed--group__display__repay-title {
  color: var(--slate-700);
}
.result__completed--group__display__repay-monthly-value {
  color: var(--lime);
  font-size: 2.5rem;
}
@media (min-width: 40em) {
  .result__completed--group__display__repay-monthly-value {
    font-size: 3.5rem;
  }
}
.result__completed--group__display__repay-overall-value {
  color: var(--white);
  font-size: 1.25rem;
}
@media (min-width: 40em) {
  .result__completed--group__display__repay-overall-value {
    font-size: 1.5rem;
  }
}
.result.is-active {
  justify-content: flex-start;
}
.result.is-active .result__empty {
  display: none;
}
.result.is-active .result__completed {
  display: flex;
}

.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: hsl(228, 45%, 44%);
}